@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:400,200,200italic,300,300italic,400italic,600,600italic,700,700italic);
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block; }

body {
  line-height: 1;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 400;
  color: #3c3c3c; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none !important;
  transition: all 0.3s ease-in-out;
  -webkit-transition: all 0.3s ease-in-out;
  -moz-transition: all 0.3s ease-in-out;
  -ms-transition: all 0.3s ease-in-out;
  -o-transition: all 0.3s ease-in-out;
  outline: none; }

p {
  font-size: 15px;
  color: #3c3c3c;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400; }

.title {
  font-weight: 700; }

::-moz-selection {
  color: #fff;
  background: #458eff; }

::selection {
  color: #fff;
  background: #458eff; }

.active {
  color: #458eff !important; }

.on {
  color: #458eff !important; }

.section-title {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 700;
  color: #3c3c3c;
  font-size: 60px;
  text-align: center;
  text-transform: uppercase; }

.section-title-white {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 700;
  color: #fff;
  font-size: 60px;
  text-align: center;
  text-transform: uppercase; }

.section-title-br {
  border-top: 1px solid #458eff;
  height: 1px;
  margin: 15px auto 0;
  position: relative;
  width: 8%; }

.ru-btn {
  color: #fff;
  background: transparent;
  border: 1px solid #fff;
  font-size: 17px;
  padding: 10px 12px;
  font-weight: normal;
  margin: 6px 0;
  margin-right: 12px;
  display: inline-block;
  text-decoration: none;
  font-family: "Source Sans Pro", sans-serif;
  min-width: 120px;
  border-radius: 20px; }

.ru-btn:hover, .ru-btn:active, .ru-btn:focus {
  color: #fff;
  background: #458eff;
  border: 1px solid #458eff; }

.ru-btn-sm {
  color: #3c3c3c;
  background: transparent;
  border: 1px solid #3c3c3c;
  font-size: 17px;
  padding: 6px 10px;
  font-weight: normal;
  margin: 6px 0;
  margin-right: 12px;
  display: inline-block;
  text-decoration: none;
  font-family: "Source Sans Pro", sans-serif;
  min-width: 80px; }

.ru-btn-sm:hover, .ru-btn-sm:active, .ru-btn-sm:focus {
  color: #fff;
  background: #458eff;
  border: 1px solid #458eff; }

.top-space {
  margin-top: 4%; }

.no-padding {
  padding: 0 !important; }

/*-----------Navigation-----------*/
#navigation {
  background-color: #fff;
  border-color: #fff;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 300;
  height: 70px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2); }

.navbar-default .navbar-nav > li > a {
  color: #3c3c3c;
  text-align: center; }

.navbar-default .navbar-nav > li > a:hover {
  color: #458eff; }

.container-fluid > .navbar-collapse, .container-fluid > .navbar-header, .container > .navbar-collapse, .container > .navbar-header {
  margin-top: 10px; }

.navbar-brand {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 600;
  color: #3c3c3c;
  font-size: 28px; }

.navbar-brand img {
  position: relative;
  bottom: 9.5px; }

.navbar-default .navbar-collapse, .navbar-default .navbar-form {
  border-color: #e7e7e7;
  background-color: #fff; }

.navbar-default .navbar-nav > .active > a, .navbar-default .navbar-nav > .active > a:focus, .navbar-default .navbar-nav > .active > a:hover {
  color: #458eff;
  background-color: #FFFFFF; }

/*-----------Home Section-----------*/
#home {
  width: 100%;
  min-height: 100vh;
  background: url("../img/hero-img.jpg") no-repeat;
  background-position: top center;
  background-size: cover;
  padding-top: 450px;
  text-align: center; }

#home h3 {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 800;
  color: #fff;
  font-size: 55px;
  text-transform: uppercase;
  line-height: 65px; }

#home h3 span {
  color: #458eff; }

#home p {
  color: #fff;
  font-size: 23px;
  font-weight: 300;
  margin-top: 1%; }

#home a {
  margin-top: 3%; }

/*-----------product Section-----------*/
#product {
  width: 100%;
  min-height: 620px;
  padding-top: 5%; }

.product-desc {
  padding-top: 3%; }

.product-desc h3 {
  color: #3c3c3c;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 600;
  font-size: 25px;
  margin-bottom: 20px; }

.product-desc h2 {
  color: #3c3c3c;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 400;
  font-size: 18px;
  margin-top: 1.5em;
  line-height: 26px; }

.product-desc p {
  color: #3c3c3c;
  font-family: "Source Sans Pro", sans-serif;
  font-weight: 300;
  font-size: 15px;
  margin-top: .75em;
  line-height: 26px;
  padding-right: 20px; }

.product-desc ul {
  list-style-type: disc;
  padding-left: 20px; }
  .product-desc ul li {
    margin-bottom: 10px; }

.super-script {
  font-size: 12px; }

.abt-feature {
  padding-top: 5%; }

/*-----------customize Section-----------*/
#customize {
  width: 100%;
  min-height: 650px;
  padding-top: 4%;
  background-color: #fcfcfc; }

.colorpicker h3 {
  color: #3c3c3c;
  font-family: Source Sans Pro,sans-serif;
  font-weight: 600;
  font-size: 25px;
  margin-bottom: 30px;
  text-transform: uppercase; }

.customize-content {
  padding-top: 5%; }

.colorpicker {
  margin-top: 40px;
  margin-bottom: 60px; }

.img-container {
  display: inline-block;
  position: relative;
  width: 100%;
  height: 400px; }

#board-side {
  min-width: 100%;
  max-width: 100%; }

.overlay {
  position: absolute !important;
  top: 0px;
  left: 0px; }

.thumbnail {
  border: 0 !important;
  background-color: transparent; }

.circle {
  margin: auto;
  -moz-border-radius: 65px/65px;
  -webkit-border-radius: 65px 65px;
  border-radius: 65px/65px;
  width: 65px;
  height: 65px; }

.green {
  background: #7BF32C; }

.purple {
  background: #FF32FF; }

.teal {
  background: #33FFFF; }

.none {
  background: #FFFFFF;
  border: 3px solid #000000; }

/*-----------specifications Section-----------*/
#specifications {
  width: 100%;
  min-height: 650px;
  padding-top: 4%;
  background-color: #fcfcfc; }

.specifications-content {
  padding-top: 5%; }

#specifications h2 {
  text-transform: uppercase; }

#specifications p {
  margin-bottom: 40px; }

.home-center {
  background-color: #24caff;
  -webkit-box-flex: 2;
  -ms-flex: 2;
  flex: 2;
  height: 100vh;
  width: 100%; }

.row-full {
  width: 100vw;
  position: relative;
  margin-left: -50vw;
  height: 600px;
  margin-top: 100px;
  left: 50%; }

/*-----------Contact Section-----------*/
#contact {
  width: 100%;
  min-height: 570px;
  padding-top: 400px; }

.line-spacer {
  margin-top: 7%; }

.contact-content {
  padding-top: 3%; }

.contact-desc {
  padding-top: 11.5%; }

.contact-desc h3 {
  font-size: 25px;
  font-weight: 400; }

.contact-desc p {
  font-size: 18px;
  font-weight: 400;
  margin-top: 1.5%;
  line-height: 23px;
  margin-left: 2%; }

.fa-icon-1 {
  margin-right: 23px; }

.fa-icon-2 {
  margin-right: 15px; }

.fa-icon-3 {
  margin-right: 20px; }

.form-group {
  margin-top: 4%; }

/*-----------Footer-----------*/
.footer-divider {
  width: 100%;
  height: 2px;
  background-color: #dedede;
  margin-bottom: 10px; }

footer {
  width: 100%;
  min-height: 60px; }

footer p {
  text-align: center;
  color: #3c3c3c;
  line-height: 60px;
  font-weight: 400; }

/*-----------Back to Top-----------*/
#back-to-top {
  position: fixed;
  bottom: 13px;
  right: 15px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  text-align: center;
  line-height: 30px;
  background: #458eff;
  color: #fff;
  cursor: pointer;
  border: 0;
  text-decoration: none;
  transition: opacity 0.3s ease-out;
  transition: background-color 0.3s ease-out;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.26);
  opacity: 0; }

#back-to-top:hover {
  background: #3c3c3c; }

#back-to-top.show {
  opacity: 1; }

#back-to-top i {
  line-height: 40px;
  font-size: 18px; }

/*----------- Modals ------------*/
.modal-text-title {
  margin-top: 30px;
  margin-bottom: 30px;
  text-align: center;
  font-weight: 500; }

/*----------- Margins-----------*/
.mb-50 {
  margin-bottom: 50px; }

/*----------- Media Queries-----------*/
@media (max-width: 991px) {
  .service-box {
    margin-top: 4%; } }

@media (max-width: 768px) {
  .service-box {
    margin-top: 4%; }
  #home {
    padding-top: 45%; } }

@media (max-width: 480px) {
  .section-title {
    font-size: 45px; }
  .service-box {
    margin-top: 4%; }
  #home {
    padding-top: 45%; } }

@media (max-width: 360px) {
  .section-title {
    font-size: 35px; }
  .service-box {
    margin-top: 4%; }
  #home {
    padding-top: 45%; } }
